.middle-section {
    position: relative;
    padding: 174px 0px;

    h2 {
        flex-basis: 50%;
        padding-top: 10px;
        padding-inline-end: 51px;
    }
    ul {
        flex-basis: 50%;
        gap: 44px;

        li {
            div {
                float: revert;
                h3 {
                    color: $clr3;
                }
            }
            img {
                width: 72px;
                margin-inline-end: 38px;
                float: left;
            }
        }
    }
    .bg-pattern1 {
        position: absolute;
        width: 200px;
        bottom: 0;
        right: 0;
        transform: translateX(50%);
    }
}