* {
    box-sizing: border-box;
}

html {
    font-family: livvic-semibold, sans-serif;
    line-height: 1.75;
    color: $clr2;
}

body {
    margin: 0;
    color: $clr4;
}

img {
    width: 100%;
}

h1,h2,h3,h4,h5,h6 {
    font-weight: 400;
    margin: 0;
    font-family: livvic-bold, sans-serif;
}

h1 {
    font-size: 6.25rem;
    line-height: 6.25rem;
}

h2 {
    font-size:  3em;
    line-height: 1em;
}

p {
    margin: 0;
    font-family: livvic-semibold, sans-serif;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

a {
    color: inherit;
    text-decoration: none;
}

.bg1 {
    background-color: $clr1;
}

.bg2 {
    background-color: $clr2;
}

.bg3 {
    background-color: $clr6;
}