.bottom-section {
    height: 744px;
    position: relative;

    h2 {
        text-align: center;

        span {
            color: $clr5;
        }
    }
    .testemonials {
        margin-top: 40px;

        > div {
            background-image: url(../../images/icon-quotes.svg);
            background-repeat: no-repeat;
            background-position: top center;
            flex-basis: 33%;

            p {
                text-align: center;

                &:first-child {
                    padding-top: 30px;
                }
                &:nth-child(2) {
                    padding-top: 30px;
                }
            }

            img {
                width: 62px;
                border: 1px solid $clr4;
                border-radius: 50%;
                margin-top: 30px;
            }

            .by {
                color: $clr5;
            }
        }
    }
    .bg-pattern1 {
        grid-column: 1;
        position: absolute;
        top: 0;
        left: 0;
        width: 147px;
    }
    .bg-pattern2 { 
        position: absolute;
        bottom: 0;
        right: 0;
        width: 200px;
    }
}